* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: #808080;
  font-family: "myriad-pro", Helvetica, Arial, sans-serif;
  background-color: #ebebeb;
}
.Nav_top {
  width: 100%;
  background-color: #fff;
  padding: 0 50px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.C_logo {
  padding-top: 40px;
}
.Need_hlp {
  display: flex;
  justify-content: flex-end;
}
.Need_hlp a {
  text-decoration: none;
  color: #008bdf;
}
.Need_hlp p {
  width: 280px;
  background-color: rgba(128, 128, 128, 0.527);
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 0 10px 10px;
}
.Menu,
.Survey_mid {
  display: none;
}
.Company_list {
  width: 100%;
  height: 100px;
  background-color: #60b9ee;
  display: flex;
  padding: 0 4rem;
  align-items: center;
  justify-content: space-between;
}
.Company_location {
  color: #fff;
}
.Company_list .list ol {
  display: flex;
}
.Company_list .list ol li {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
  color: #fff;
}
.list .fa-long-arrow-alt-right {
  padding: 4px 4px 0 4px;
  font-size: 25px;
  cursor: default;
}
.Survey_Box {
  border: 1px solid #ebebeb;
  margin: 25px 35px;
  border-radius: 5px;
  padding: 10px 0;
  background-color: #fff;
  height: auto;
  box-shadow: 1px 1px 5px #808080;
}
.Survey_Q {
  text-align: center;
  font-size: 27px;
  color: #0093dd;
  padding: 10px;
  border-bottom: 1px solid #80808081;
}
.Survey_p {
  margin: 25px;
  font-size: 22px;
}
.Survey_p :nth-child(1) {
  margin-bottom: 30px;
}
.Agree_DisAgree {
  width: 100%;
  height: auto;
}
.A_D_h3 {
  text-align: center;
  color: #0093dd;
  font-size: 20px;
}
.Agree_DisAgree_btns {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
  padding-bottom: 20px;
}
.DisAgree_btns,
.Agree_btns {
  display: flex;
}
.DisAgree_btns button span,
.Agree_btns button span {
  color: #60b9ee;
  font-size: 19px;
  padding: 0 3px;
}
.DisAgree_btns button,
.Agree_btns button {
  border: none;
  background-color: #fff;
  width: 240px;
  margin: 0 10px;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #808080;
  cursor: pointer;
  padding: 10px;
  height: 60px;
  border-radius: 10px;
}
.Agree_btns button {
  margin: 0 10px;
}
.range_input {
  text-align: center;
}
.range_input input {
  width: 250px;
}
.opinions {
  border: 1px solid #ebebeb;
  margin: 20px 35px;
  border-radius: 5px;
  padding: 20px 0;
  background-color: #fff;
  height: auto;
  box-shadow: 1px 1px 5px #808080;
}
.opinions > :nth-child(1) {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 20px;
}
.opinions p {
  padding: 3px 0;
}
.opinions p span {
  padding-left: 5px;
}
.Survey_buttons {
  margin: 20px 0;
  display: flex;
  padding: 2rem 6rem 0 6rem;
  justify-content: space-between;
  align-items: center;
}

.Prvs_btn button {
  width: 170px;
}
.submit_btn button {
  width: 210px;
}
.Prvs_btn button,
.submit_btn button {
  padding: 12px;
  display: flex;
  font-size: 18px;
  border-radius: 25px;
  font-weight: bold;
  cursor: pointer;
  border: none;
  background-color: #80c844;
  color: #fff;
  align-items: center;
  justify-content: space-between;
}
.submit_btn button {
  background-color: #8080805b;
}
